// 矩阵工具箱
class MatrixToolkit {
    make(value = 0) {
        return Array.from({ length: 9 }, () => this.makeRow(value));
    }
    makeRow(value = 0) {
        const array = new Array(9);
        array.fill(value);
        return array;
    }
    // Fisher-Yates 洗牌算法，Lodash 中有实现
    // 传入一个数组，对数组元素洗牌之后，再将其返回
    shuffle(array) {
        const length = array.length;
        const endIndex = length - 1;
        for (let i = 0; i < endIndex; i++) {
            const j = i + Math.floor(Math.random() * (length - i));
            [array[i], array[j]] = [array[j], array[i]];
        }
        return array;
    }
    // 检查某个位置是否可以填入 n
    checkFillable(matrix, n, rowIndex, colIndex) {
        const boxCoor = boxToolkit.convertToBoxIndex(rowIndex, colIndex);
        const boxCells = boxToolkit.getBoxCells(matrix, boxCoor.boxIndex);
        for (let i = 0; i < 9; i++) {
            if (matrix[i][colIndex] === n
                || matrix[rowIndex][i] === n
                || boxCells[i] === n) {
                return false;
            }
        }
        return true;
    }
}
